.summary {
	max-width: 120rem;
	min-height: 20rem;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}

.department {
	flex: 0 0 auto;
	border: 1px solid var(--color-primary);
	border-radius: 1rem;
	display: flex;
	flex-direction: column;
	margin: 3rem;
	width: 20rem;
	overflow: hidden;

	&__title {
		background-color: var(--color-primary);
		color: var(--color-tertiary);
		font-size: 1.8rem;
		font-weight: bold;
		letter-spacing: 2px;
		padding: 1rem;
	}

	&__currencies {
		display: flex;
		flex-direction: column;
		color: var(--color-secondary);
		font-size: 2rem;
	}

	&__currency {
		padding: 1rem;
		border-bottom: 1px solid var(--color-grey);

		&:hover {
			background-color: var(--color-grey-dark);
		}
	}
}
